@import url('https://fonts.googleapis.com/css2?family=Orbitron:wght@400;500;600;700;800;900&family=Rajdhani:wght@300;400;500;600;700&family=Inter:wght@300;400;500;600;700;800;900&display=swap');

:root {
  /* Neon Gaming Colors - Dark Theme */
  --neon-cyan: #00ffff;
  --neon-purple: #8a2be2;
  --neon-pink: #ff1493;
  --neon-green: #39ff14;
  --neon-orange: #ff4500;
  --neon-blue: #0080ff;
  --neon-yellow: #ffff00;
  
  /* Dark Theme Colors */
  --bg-primary: #0c0c0c;
  --bg-secondary: #1a1a2e;
  --bg-tertiary: #16213e;
  --glass-bg: rgba(15, 15, 35, 0.8);
  --glass-border: rgba(255, 255, 255, 0.1);
  --glass-glow: rgba(0, 255, 255, 0.3);
  --text-primary: #ffffff;
  --text-secondary: rgba(255, 255, 255, 0.8);
  --text-muted: rgba(255, 255, 255, 0.6);
  --text-neon: #00ffff;
  
  /* Gaming Gradients */
  --gaming-gradient: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  --neon-gradient: linear-gradient(45deg, var(--neon-cyan), var(--neon-purple));
  --cyber-gradient: linear-gradient(135deg, var(--bg-primary) 0%, var(--bg-secondary) 50%, var(--bg-tertiary) 100%);
  --holographic: linear-gradient(45deg, #ff0080, #ff8c00, #40e0d0, #ff0080);
  --rainbow-gradient: linear-gradient(45deg, #ff0080, #ff8c00, #40e0d0, #8a2be2, #39ff14, #ffff00, #ff0080);
  
  /* Shadows & Effects */
  --neon-shadow: 0 0 20px var(--neon-cyan), 0 0 40px var(--neon-cyan), 0 0 60px var(--neon-cyan);
  --cyber-shadow: 0 8px 32px rgba(0, 0, 0, 0.5);
  --hologram-shadow: 0 0 30px rgba(64, 224, 208, 0.5);
  
  /* Particle Colors */
  --particle-color-1: var(--neon-cyan);
  --particle-color-2: var(--neon-purple);
  --particle-color-3: var(--neon-pink);
  --particle-color-4: var(--neon-green);
}

/* Light Theme Variables */
[data-theme="light"] {
  --bg-primary: #f8fafc;
  --bg-secondary: #e2e8f0;
  --bg-tertiary: #cbd5e1;
  --glass-bg: rgba(255, 255, 255, 0.8);
  --glass-border: rgba(0, 0, 0, 0.1);
  --glass-glow: rgba(0, 255, 255, 0.2);
  --text-primary: #1a202c;
  --text-secondary: rgba(26, 32, 44, 0.8);
  --text-muted: rgba(26, 32, 44, 0.6);
  --text-neon: #0080ff;
  --cyber-gradient: linear-gradient(135deg, var(--bg-primary) 0%, var(--bg-secondary) 50%, var(--bg-tertiary) 100%);
  --cyber-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Inter', 'Rajdhani', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
  background: var(--cyber-gradient);
  overflow: hidden;
  position: relative;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

/* Theme Toggle Button */
.theme-toggle {
  position: fixed;
  top: 20px;
  right: 20px;
  z-index: 9999;
  background: var(--glass-bg);
  backdrop-filter: blur(20px);
  border: 2px solid var(--glass-border);
  border-radius: 50px;
  width: 60px;
  height: 30px;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
}

.theme-toggle::before {
  content: 'üåô';
  position: absolute;
  left: 5px;
  top: 50%;
  transform: translateY(-50%);
  font-size: 16px;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

[data-theme="light"] .theme-toggle::before {
  content: '‚òÄÔ∏è';
  left: 25px;
}

.theme-toggle:hover {
  transform: scale(1.1);
  box-shadow: 0 0 20px var(--glass-glow);
}

/* Enhanced Particles System */
.particles-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: -1;
  overflow: hidden;
}

.particle {
  position: absolute;
  border-radius: 50%;
  pointer-events: none;
  opacity: 0.6;
}

.particle-1 {
  width: 4px;
  height: 4px;
  background: var(--particle-color-1);
  animation: float1 8s infinite linear;
  box-shadow: 0 0 10px var(--particle-color-1);
}

.particle-2 {
  width: 6px;
  height: 6px;
  background: var(--particle-color-2);
  animation: float2 12s infinite linear;
  box-shadow: 0 0 15px var(--particle-color-2);
}

.particle-3 {
  width: 3px;
  height: 3px;
  background: var(--particle-color-3);
  animation: float3 10s infinite linear;
  box-shadow: 0 0 8px var(--particle-color-3);
}

.particle-4 {
  width: 5px;
  height: 5px;
  background: var(--particle-color-4);
  animation: float4 15s infinite linear;
  box-shadow: 0 0 12px var(--particle-color-4);
}

@keyframes float1 {
  0% {
    transform: translateY(100vh) translateX(0px) rotate(0deg);
    opacity: 0;
  }
  10% {
    opacity: 1;
  }
  90% {
    opacity: 1;
  }
  100% {
    transform: translateY(-10px) translateX(100px) rotate(360deg);
    opacity: 0;
  }
}

@keyframes float2 {
  0% {
    transform: translateY(100vh) translateX(0px) rotate(0deg) scale(1);
    opacity: 0;
  }
  10% {
    opacity: 0.8;
  }
  50% {
    transform: translateY(50vh) translateX(-50px) rotate(180deg) scale(1.5);
  }
  90% {
    opacity: 0.8;
  }
  100% {
    transform: translateY(-10px) translateX(-100px) rotate(360deg) scale(1);
    opacity: 0;
  }
}

@keyframes float3 {
  0% {
    transform: translateY(100vh) translateX(50px) rotate(0deg);
    opacity: 0;
  }
  15% {
    opacity: 1;
  }
  85% {
    opacity: 1;
  }
  100% {
    transform: translateY(-10px) translateX(-150px) rotate(-360deg);
    opacity: 0;
  }
}

@keyframes float4 {
  0% {
    transform: translateY(100vh) translateX(-30px) rotate(0deg) scale(0.5);
    opacity: 0;
  }
  20% {
    opacity: 0.9;
    transform: translateY(80vh) translateX(-30px) rotate(60deg) scale(1.2);
  }
  80% {
    opacity: 0.9;
    transform: translateY(20vh) translateX(30px) rotate(300deg) scale(0.8);
  }
  100% {
    transform: translateY(-10px) translateX(60px) rotate(360deg) scale(0.5);
    opacity: 0;
  }
}

/* Enhanced Background Animations */
body::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: 
    radial-gradient(circle at 20% 50%, rgba(0, 255, 255, 0.15) 0%, transparent 50%),
    radial-gradient(circle at 80% 20%, rgba(255, 20, 147, 0.15) 0%, transparent 50%),
    radial-gradient(circle at 40% 80%, rgba(138, 43, 226, 0.15) 0%, transparent 50%),
    radial-gradient(circle at 60% 30%, rgba(57, 255, 20, 0.1) 0%, transparent 40%);
  z-index: -2;
  animation: backgroundWave 12s ease-in-out infinite;
}

@keyframes backgroundWave {
  0%, 100% { 
    opacity: 0.6;
    transform: scale(1) rotate(0deg);
  }
  25% {
    opacity: 0.8;
    transform: scale(1.1) rotate(5deg);
  }
  50% { 
    opacity: 1;
    transform: scale(1.05) rotate(0deg);
  }
  75% {
    opacity: 0.9;
    transform: scale(0.95) rotate(-3deg);
  }
}

/* Enhanced Grid Background */
body::after {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: 
    linear-gradient(rgba(0, 255, 255, 0.05) 1px, transparent 1px),
    linear-gradient(90deg, rgba(0, 255, 255, 0.05) 1px, transparent 1px),
    radial-gradient(circle at 25% 25%, rgba(255, 20, 147, 0.03) 2px, transparent 2px),
    radial-gradient(circle at 75% 75%, rgba(138, 43, 226, 0.03) 2px, transparent 2px);
  background-size: 60px 60px, 60px 60px, 120px 120px, 80px 80px;
  z-index: -1;
  animation: gridFlow 25s linear infinite;
}

@keyframes gridFlow {
  0% { 
    transform: translate(0, 0) rotate(0deg);
    opacity: 0.3;
  }
  25% {
    transform: translate(30px, 15px) rotate(1deg);
    opacity: 0.5;
  }
  50% {
    transform: translate(60px, 30px) rotate(0deg);
    opacity: 0.4;
  }
  75% {
    transform: translate(30px, 45px) rotate(-1deg);
    opacity: 0.6;
  }
  100% { 
    transform: translate(60px, 60px) rotate(0deg);
    opacity: 0.3;
  }
}

/* Login Page Enhancements */
.login-page {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  position: relative;
  overflow: hidden;
}

.login-card {
  background: var(--glass-bg);
  backdrop-filter: blur(25px);
  border: 2px solid var(--glass-border);
  border-radius: 25px;
  padding: 40px;
  width: 450px;
  box-shadow: var(--cyber-shadow), inset 0 1px 0 rgba(255, 255, 255, 0.1);
  position: relative;
  overflow: hidden;
  animation: loginSlideIn 1s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

.login-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(0, 255, 255, 0.15), transparent);
  animation: shimmer 4s infinite;
}

@keyframes loginSlideIn {
  0% { 
    opacity: 0;
    transform: translateY(100px) scale(0.8) rotateX(30deg);
  }
  100% { 
    opacity: 1;
    transform: translateY(0) scale(1) rotateX(0deg);
  }
}

@keyframes shimmer {
  0% { left: -100%; }
  100% { left: 100%; }
}

.login-header {
  text-align: center;
  margin-bottom: 30px;
}

.login-icon {
  font-size: 4rem;
  margin-bottom: 20px;
  display: inline-block;
  animation: iconFloat 4s ease-in-out infinite;
  filter: drop-shadow(0 0 20px var(--neon-cyan));
  background: var(--rainbow-gradient);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

@keyframes iconFloat {
  0%, 100% { 
    transform: translateY(0px) rotate(0deg) scale(1);
  }
  25% {
    transform: translateY(-8px) rotate(2deg) scale(1.05);
  }
  50% { 
    transform: translateY(-15px) rotate(0deg) scale(1.1);
  }
  75% {
    transform: translateY(-8px) rotate(-2deg) scale(1.05);
  }
}

.login-header h1 {
  font-family: 'Orbitron', monospace;
  font-size: 2.5rem;
  font-weight: 900;
  background: var(--rainbow-gradient);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  background-size: 200% 200%;
  animation: rainbowText 3s ease-in-out infinite, textGlow 2s ease-in-out infinite alternate;
  margin-bottom: 10px;
}

@keyframes rainbowText {
  0%, 100% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
}

@keyframes textGlow {
  0% { filter: drop-shadow(0 0 20px var(--neon-cyan)); }
  100% { filter: drop-shadow(0 0 40px var(--neon-cyan)) drop-shadow(0 0 60px var(--neon-purple)); }
}

/* Enhanced Form Elements */
.login-input {
  background: var(--glass-bg);
  border: 2px solid var(--glass-border);
  border-radius: 15px;
  padding: 15px 20px;
  color: var(--text-primary);
  font-size: 1rem;
  font-weight: 500;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  backdrop-filter: blur(10px);
}

.login-input:focus {
  outline: none;
  border-color: var(--neon-cyan);
  box-shadow: 0 0 25px rgba(0, 255, 255, 0.4);
  background: var(--glass-bg);
  transform: translateY(-2px) scale(1.02);
}

.login-button, .toggle-button {
  background: var(--neon-gradient);
  border: none;
  border-radius: 15px;
  padding: 15px 25px;
  color: white;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
  text-transform: uppercase;
  letter-spacing: 1px;
  background-size: 200% 200%;
  animation: gradientShift 3s ease-in-out infinite;
}

@keyframes gradientShift {
  0%, 100% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
}

.login-button::before, .toggle-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
  transition: left 0.5s;
}

.login-button:hover::before, .toggle-button:hover::before {
  left: 100%;
}

.login-button:hover, .toggle-button:hover {
  transform: translateY(-3px) scale(1.05);
  box-shadow: 0 15px 35px rgba(0, 255, 255, 0.5);
}

/* Enhanced Sidebar */
.sidebar {
  width: 80px;
  background: var(--glass-bg);
  backdrop-filter: blur(25px);
  border-right: 2px solid var(--glass-border);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px 0;
  gap: 15px;
  position: relative;
  overflow: hidden;
}

.sidebar::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(180deg, 
    rgba(0, 255, 255, 0.08) 0%,
    transparent 50%,
    rgba(255, 20, 147, 0.08) 100%
  );
  z-index: -1;
  animation: sidebarGlow 6s ease-in-out infinite alternate;
}

@keyframes sidebarGlow {
  0% { opacity: 0.5; }
  100% { opacity: 1; }
}

.app-logo {
  width: 55px;
  height: 55px;
  border-radius: 18px;
  background: var(--rainbow-gradient);
  background-size: 200% 200%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.8rem;
  color: white;
  font-weight: bold;
  margin-bottom: 20px;
  box-shadow: var(--cyber-shadow);
  animation: logoRotate 8s ease-in-out infinite, gradientShift 4s ease-in-out infinite;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

@keyframes logoRotate {
  0%, 100% { 
    transform: rotate(0deg) scale(1);
  }
  25% { 
    transform: rotate(10deg) scale(1.1);
  }
  50% { 
    transform: rotate(0deg) scale(1.15);
  }
  75% { 
    transform: rotate(-10deg) scale(1.1);
  }
}

.app-logo:hover {
  transform: scale(1.2) rotate(15deg);
  box-shadow: 0 0 40px var(--neon-cyan), 0 0 80px var(--neon-purple);
}

/* Enhanced Voice Cards */
.voice-card {
  background: var(--glass-bg);
  backdrop-filter: blur(25px);
  border: 2px solid var(--glass-border);
  border-radius: 20px;
  padding: 25px;
  text-align: center;
  position: relative;
  overflow: hidden;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;
  animation: cardSlideUp 0.6s ease-out forwards;
  opacity: 0;
}

.voice-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, 
    rgba(0, 255, 255, 0.08) 0%,
    rgba(255, 20, 147, 0.08) 100%
  );
  opacity: 0;
  transition: opacity 0.3s;
  z-index: -1;
}

.voice-card:hover::before {
  opacity: 1;
  animation: cardGlow 2s ease-in-out infinite alternate;
}

@keyframes cardGlow {
  0% { 
    background: linear-gradient(135deg, rgba(0, 255, 255, 0.08) 0%, rgba(255, 20, 147, 0.08) 100%);
  }
  100% { 
    background: linear-gradient(135deg, rgba(138, 43, 226, 0.12) 0%, rgba(57, 255, 20, 0.12) 100%);
  }
}

.voice-card:hover {
  transform: translateY(-15px) scale(1.03);
  border-color: var(--neon-cyan);
  box-shadow: 
    0 25px 50px rgba(0, 0, 0, 0.4),
    0 0 40px rgba(0, 255, 255, 0.4);
}

.voice-card.speaking {
  border-color: var(--neon-green);
  animation: speakingPulse 1s infinite, cardFloat 3s ease-in-out infinite;
  box-shadow: 0 0 50px rgba(57, 255, 20, 0.6);
}

@keyframes speakingPulse {
  0%, 100% { 
    border-color: var(--neon-green);
    box-shadow: 0 0 50px rgba(57, 255, 20, 0.6);
  }
  50% { 
    border-color: #7fff00;
    box-shadow: 0 0 80px rgba(57, 255, 20, 0.9);
    transform: scale(1.05);
  }
}

@keyframes cardFloat {
  0%, 100% { transform: translateY(0px); }
  50% { transform: translateY(-5px); }
}

/* Enhanced Names with Gradient */
.voice-name, .message-author, .user-name {
  background: var(--rainbow-gradient);
  background-size: 200% 200%;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: rainbowText 4s ease-in-out infinite;
  font-weight: 700;
  transition: all 0.3s;
}

.voice-card:hover .voice-name {
  transform: scale(1.1);
  filter: drop-shadow(0 0 15px var(--neon-cyan));
}

/* Enhanced Message Animations */
.message-item {
  display: flex;
  gap: 15px;
  margin-bottom: 20px;
  animation: messageSlideIn 0.6s cubic-bezier(0.68, -0.55, 0.265, 1.55);
  transition: all 0.3s;
}

.message-item:hover {
  transform: translateX(10px) scale(1.02);
}

.message-avatar {
  width: 45px;
  height: 45px;
  border-radius: 50%;
  background: var(--rainbow-gradient);
  background-size: 200% 200%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: bold;
  font-size: 1.1rem;
  flex-shrink: 0;
  animation: avatarGlow 4s ease-in-out infinite, gradientShift 3s ease-in-out infinite;
  transition: all 0.3s;
}

.message-item:hover .message-avatar {
  transform: scale(1.15) rotate(10deg);
  box-shadow: 0 10px 25px rgba(0, 255, 255, 0.5);
}

@keyframes avatarGlow {
  0%, 100% { 
    box-shadow: 0 5px 15px rgba(0, 255, 255, 0.4);
    filter: drop-shadow(0 0 10px var(--neon-cyan));
  }
  50% { 
    box-shadow: 0 8px 25px rgba(255, 20, 147, 0.4);
    filter: drop-shadow(0 0 15px var(--neon-pink));
  }
}

/* Enhanced Speaking Effects */
.speaking .message-text, .speaking .voice-status {
  position: relative;
  overflow: hidden;
}

.speaking .message-text::before, .speaking .voice-status::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, 
    transparent, 
    rgba(57, 255, 20, 0.3), 
    transparent
  );
  animation: speakingWave 1.5s infinite;
}

@keyframes speakingWave {
  0% { left: -100%; }
  100% { left: 100%; }
}

/* Enhanced Chat Header */
.chat-header {
  height: 70px;
  background: var(--glass-bg);
  backdrop-filter: blur(25px);
  border-bottom: 2px solid var(--glass-border);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 30px;
  animation: headerSlide 0.8s ease-out;
  position: relative;
  overflow: hidden;
}

.chat-header::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, 
    transparent, 
    rgba(0, 255, 255, 0.1), 
    transparent
  );
  animation: headerShimmer 8s infinite;
}

@keyframes headerShimmer {
  0% { left: -100%; }
  100% { left: 100%; }
}

.channel-title {
  font-family: 'Orbitron', monospace;
  background: var(--rainbow-gradient);
  background-size: 200% 200%;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.4rem;
  font-weight: 700;
  animation: rainbowText 3s ease-in-out infinite, titleGlow 4s ease-in-out infinite alternate;
  display: flex;
  align-items: center;
  gap: 12px;
}

@keyframes titleGlow {
  0% { filter: drop-shadow(0 0 10px var(--neon-cyan)); }
  100% { filter: drop-shadow(0 0 30px var(--neon-cyan)) drop-shadow(0 0 50px var(--neon-purple)); }
}

/* Responsive Design with Enhanced Animations */
@media (max-width: 1200px) {
  .voice-grid {
    grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
    gap: 20px;
  }
  
  .online-sidebar {
    width: 250px;
  }
}

@media (max-width: 768px) {
  .app-container {
    flex-direction: column;
    height: 100vh;
  }
  
  .sidebar {
    width: 100%;
    height: 70px;
    flex-direction: row;
    justify-content: space-around;
    padding: 10px;
    order: 3;
  }
  
  .particles-container {
    display: block; /* Keep particles on mobile */
  }
  
  .particle {
    animation-duration: 6s; /* Faster on mobile */
  }
}

/* Additional Particle Styles for Dynamic Effect */
.energy-orb {
  position: absolute;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: radial-gradient(circle, var(--neon-cyan) 0%, transparent 70%);
  animation: orbFloat 10s infinite ease-in-out;
  pointer-events: none;
  opacity: 0.4;
}

@keyframes orbFloat {
  0%, 100% {
    transform: translateY(0px) translateX(0px) scale(1);
    opacity: 0.4;
  }
  25% {
    transform: translateY(-30px) translateX(20px) scale(1.2);
    opacity: 0.8;
  }
  50% {
    transform: translateY(-60px) translateX(-10px) scale(0.8);
    opacity: 0.6;
  }
  75% {
    transform: translateY(-30px) translateX(-25px) scale(1.1);
    opacity: 0.9;
  }
}

/* Enhanced Connection Status with Pulse */
.connection-status {
  color: var(--neon-green);
  font-size: 0.9rem;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 8px;
  animation: statusTextGlow 3s ease-in-out infinite alternate;
}

@keyframes statusTextGlow {
  0% { 
    text-shadow: 0 0 10px var(--neon-green);
  }
  100% { 
    text-shadow: 0 0 20px var(--neon-green), 0 0 30px var(--neon-green);
  }
}

.connection-status::before {
  content: '';
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: var(--neon-green);
  animation: statusPulse 2s infinite, statusRotate 4s linear infinite;
  box-shadow: 0 0 15px var(--neon-green);
}

@keyframes statusRotate {
  0% { transform: rotate(0deg) scale(1); }
  25% { transform: rotate(90deg) scale(1.2); }
  50% { transform: rotate(180deg) scale(1); }
  75% { transform: rotate(270deg) scale(1.2); }
  100% { transform: rotate(360deg) scale(1); }
}

@keyframes statusPulse {
  0%, 100% { 
    opacity: 1;
    transform: scale(1);
    box-shadow: 0 0 15px var(--neon-green);
  }
  50% { 
    opacity: 0.7;
    transform: scale(1.5);
    box-shadow: 0 0 30px var(--neon-green), 0 0 45px var(--neon-green);
  }
}

/* Enhanced Volume Controls */
.volume-control {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-top: 15px;
  padding: 12px;
  background: var(--glass-bg);
  border-radius: 15px;
  backdrop-filter: blur(15px);
  border: 1px solid var(--glass-border);
  transition: all 0.3s;
}

.volume-control:hover {
  transform: scale(1.02);
  box-shadow: 0 5px 20px rgba(0, 255, 255, 0.2);
  border-color: var(--neon-cyan);
}

.volume-slider {
  flex: 1;
  height: 8px;
  background: linear-gradient(90deg, 
    rgba(255, 255, 255, 0.1) 0%, 
    rgba(0, 255, 255, 0.3) 100%
  );
  border-radius: 4px;
  outline: none;
  appearance: none;
  transition: all 0.3s;
}

.volume-slider:hover {
  background: linear-gradient(90deg, 
    rgba(0, 255, 255, 0.2) 0%, 
    rgba(138, 43, 226, 0.4) 100%
  );
}

.volume-slider::-webkit-slider-thumb {
  appearance: none;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: var(--rainbow-gradient);
  background-size: 200% 200%;
  cursor: pointer;
  box-shadow: 0 0 15px var(--neon-cyan);
  animation: gradientShift 3s ease-in-out infinite;
  transition: all 0.3s;
}

.volume-slider::-webkit-slider-thumb:hover {
  transform: scale(1.2);
  box-shadow: 0 0 25px var(--neon-cyan), 0 0 40px var(--neon-purple);
}

.volume-value {
  background: var(--rainbow-gradient);
  background-size: 200% 200%;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  font-weight: 700;
  font-size: 1rem;
  min-width: 45px;
  text-align: center;
  animation: rainbowText 4s ease-in-out infinite;
}

/* Enhanced Online Sidebar */
.online-sidebar {
  width: 280px;
  background: var(--glass-bg);
  backdrop-filter: blur(25px);
  border-left: 2px solid var(--glass-border);
  display: flex;
  flex-direction: column;
  position: relative;
  overflow: hidden;
  animation: sidebarSlideIn 0.8s ease-out;
}

.online-sidebar::before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(270deg, 
    rgba(0, 255, 255, 0.05) 0%,
    transparent 50%,
    rgba(255, 20, 147, 0.05) 100%
  );
  z-index: -1;
  animation: sidebarGlow 8s ease-in-out infinite alternate;
}

/* Enhanced Online User Items */
.online-user-item {
  display: flex;
  align-items: center;
  padding: 15px 20px;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

.online-user-item::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, 
    transparent, 
    rgba(0, 255, 255, 0.15), 
    transparent
  );
  transition: left 0.6s cubic-bezier(0.4, 0, 0.2, 1);
}

.online-user-item:hover::before {
  left: 100%;
}

.online-user-item:hover {
  background: linear-gradient(90deg, 
    rgba(0, 255, 255, 0.1) 0%, 
    rgba(138, 43, 226, 0.1) 100%
  );
  border-left: 4px solid var(--neon-cyan);
  transform: translateX(8px) scale(1.02);
  box-shadow: inset 0 0 30px rgba(0, 255, 255, 0.1);
}

.user-avatar {
  width: 45px;
  height: 45px;
  border-radius: 50%;
  background: var(--rainbow-gradient);
  background-size: 200% 200%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: bold;
  font-size: 1.1rem;
  margin-right: 15px;
  position: relative;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
  transition: all 0.4s;
  animation: gradientShift 4s ease-in-out infinite;
}

.online-user-item:hover .user-avatar {
  transform: scale(1.15) rotate(5deg);
  box-shadow: 0 10px 25px rgba(0, 255, 255, 0.5);
}

/* Enhanced User Status Indicators */
.user-status-indicator {
  position: absolute;
  bottom: -2px;
  right: -2px;
  width: 14px;
  height: 14px;
  border-radius: 50%;
  border: 3px solid var(--glass-bg);
  transition: all 0.3s;
}

.user-status-indicator.online {
  background: var(--neon-green);
  animation: statusPulse 3s infinite;
  box-shadow: 0 0 10px var(--neon-green);
}

.user-status-indicator.speaking {
  background: var(--neon-cyan);
  animation: speakingRipple 0.8s infinite;
  box-shadow: 0 0 15px var(--neon-cyan);
}

@keyframes speakingRipple {
  0% { 
    transform: scale(1);
    box-shadow: 0 0 15px var(--neon-cyan);
  }
  50% { 
    transform: scale(1.4);
    box-shadow: 0 0 25px var(--neon-cyan), 0 0 35px var(--neon-cyan);
  }
  100% { 
    transform: scale(1);
    box-shadow: 0 0 15px var(--neon-cyan);
  }
}

/* Enhanced Message Input Area */
.message-input-area {
  padding: 25px;
  background: var(--glass-bg);
  backdrop-filter: blur(25px);
  border-top: 2px solid var(--glass-border);
  position: relative;
  overflow: hidden;
}

.message-input-area::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, 
    transparent, 
    rgba(0, 255, 255, 0.08), 
    transparent
  );
  animation: inputShimmer 6s infinite;
}

@keyframes inputShimmer {
  0% { left: -100%; }
  100% { left: 100%; }
}

.message-input-group {
  display: flex;
  gap: 15px;
  align-items: center;
  position: relative;
  z-index: 1;
}

.message-input {
  flex: 1;
  background: var(--glass-bg);
  border: 2px solid var(--glass-border);
  border-radius: 25px;
  padding: 15px 20px;
  color: var(--text-primary);
  font-size: 1rem;
  font-weight: 500;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  backdrop-filter: blur(15px);
}

.message-input:focus {
  outline: none;
  border-color: var(--neon-cyan);
  box-shadow: 0 0 30px rgba(0, 255, 255, 0.4);
  background: var(--glass-bg);
  transform: translateY(-2px) scale(1.01);
}

.message-input:focus::placeholder {
  transform: translateY(-2px);
  color: var(--neon-cyan);
}

.send-button {
  background: var(--rainbow-gradient);
  background-size: 200% 200%;
  border: none;
  border-radius: 25px;
  padding: 15px 25px;
  color: white;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
  animation: gradientShift 3s ease-in-out infinite;
}

.send-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.4), transparent);
  transition: left 0.5s;
}

.send-button:hover::before {
  left: 100%;
}

.send-button:hover {
  transform: translateY(-3px) scale(1.05);
  box-shadow: 0 10px 30px rgba(0, 255, 255, 0.5);
}

.send-button:active {
  transform: translateY(-1px) scale(0.98);
}

/* Enhanced Buttons with Hover Effects */
.channel-button, .stream-button, .logout-button {
  width: 55px;
  height: 55px;
  border: 2px solid var(--glass-border);
  border-radius: 18px;
  background: var(--glass-bg);
  backdrop-filter: blur(15px);
  color: var(--text-secondary);
  font-size: 1.4rem;
  cursor: pointer;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
}

.channel-button::before, .stream-button::before, .logout-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle, rgba(0, 255, 255, 0.15) 0%, transparent 70%);
  opacity: 0;
  transition: all 0.3s;
  animation: buttonGlow 4s ease-in-out infinite alternate;
}

@keyframes buttonGlow {
  0% { background: radial-gradient(circle, rgba(0, 255, 255, 0.1) 0%, transparent 70%); }
  100% { background: radial-gradient(circle, rgba(255, 20, 147, 0.15) 0%, transparent 70%); }
}

.channel-button:hover::before, .stream-button:hover::before, .logout-button:hover::before {
  opacity: 1;
}

.channel-button:hover, .stream-button:hover, .logout-button:hover {
  transform: translateY(-8px) scale(1.15) rotate(5deg);
  border-color: var(--neon-cyan);
  color: var(--neon-cyan);
  box-shadow: 0 15px 30px rgba(0, 255, 255, 0.4);
}

.channel-button.active, .stream-button.active {
  background: var(--rainbow-gradient);
  background-size: 200% 200%;
  color: white;
  border-color: transparent;
  box-shadow: var(--neon-shadow);
  animation: activeGlow 2s ease-in-out infinite alternate, gradientShift 4s ease-in-out infinite;
}

@keyframes activeGlow {
  0% { 
    box-shadow: 0 0 25px var(--neon-cyan);
    transform: scale(1);
  }
  100% { 
    box-shadow: 0 0 40px var(--neon-cyan), 0 0 60px var(--neon-purple);
    transform: scale(1.05);
  }
}

/* Enhanced Modal Styles */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.85);
  backdrop-filter: blur(15px);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  animation: modalFadeIn 0.4s ease-out;
}

.modal-content {
  background: var(--glass-bg);
  backdrop-filter: blur(25px);
  border: 2px solid var(--glass-border);
  border-radius: 25px;
  padding: 40px;
  max-width: 600px;
  width: 90%;
  max-height: 80vh;
  overflow-y: auto;
  position: relative;
  animation: modalSlideIn 0.5s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

.modal-content::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(45deg, 
    rgba(0, 255, 255, 0.05) 0%, 
    rgba(255, 20, 147, 0.05) 50%,
    rgba(138, 43, 226, 0.05) 100%
  );
  border-radius: 25px;
  z-index: -1;
  animation: modalGlow 6s ease-in-out infinite alternate;
}

@keyframes modalGlow {
  0% { opacity: 0.5; }
  100% { opacity: 1; }
}

.modal-content h2 {
  background: var(--rainbow-gradient);
  background-size: 200% 200%;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 25px;
  text-align: center;
  font-family: 'Orbitron', monospace;
  animation: rainbowText 3s ease-in-out infinite, titleGlow 4s ease-in-out infinite alternate;
}

/* Scrollbar Enhancements */
::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: var(--glass-bg);
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  background: var(--rainbow-gradient);
  background-size: 200% 200%;
  border-radius: 10px;
  animation: gradientShift 4s ease-in-out infinite;
}

::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(45deg, var(--neon-cyan), var(--neon-purple), var(--neon-pink));
  box-shadow: 0 0 15px var(--neon-cyan);
}

/* Additional Particle Effects */
.floating-icon {
  position: fixed;
  font-size: 24px;
  pointer-events: none;
  z-index: 0;
  opacity: 0.3;
  animation: floatingIcon 12s infinite linear;
}

@keyframes floatingIcon {
  0% {
    transform: translateY(100vh) translateX(0px) rotate(0deg);
    opacity: 0;
  }
  10% {
    opacity: 0.3;
  }
  90% {
    opacity: 0.3;
  }
  100% {
    transform: translateY(-50px) translateX(50px) rotate(360deg);
    opacity: 0;
  }
}

/* Performance Optimizations */
* {
  will-change: auto;
}

.login-card,
.voice-card,
.message-item,
.online-user-item {
  transform: translateZ(0);
  backface-visibility: hidden;
  perspective: 1000px;
}

/* High FPS Animations */
@media (min-width: 1024px) {
  .particle,
  .energy-orb,
  .floating-icon {
    animation-timing-function: linear;
    will-change: transform, opacity;
  }
  
  body::before {
    animation-duration: 8s; /* Faster on desktop */
  }
  
  body::after {
    animation-duration: 15s; /* Smoother grid movement */
  }
}